import React ,{useEffect, useState} from 'react';
import CreateTask from '../models/CreateTask';
import Card from './Card';
const Todolist = () => {
    const [modal, setModal] = useState(false);
    const [taskList , setTaskList] =useState([]);
    useEffect(()=>{
        let arr= localStorage.getItem('taskList');
        if (arr) {
            let obj = JSON.parse(arr);
            setTaskList(obj)
        }
    },[]) ;
    //fun edite Task
     const updateListArray=(obj,index)=>{
        let templist = taskList;
        templist[index]=obj;
        localStorage.setItem("taskList",JSON.stringify(templist));
        setTaskList(templist);
        window.location.reload();
     }
   
   //fun delete Task
   const deleteTask=(index)=>{
    let templist = taskList;
    templist.splice(index,1);
    localStorage.setItem("taskList",JSON.stringify(templist));
    setTaskList(templist);
    window.location.reload();
   };
   //
    const toggle = () => setModal(!modal);
    // function to saveTasks
    const saveTask = (taskObj) => {
        let templist = taskList;
        templist.push(taskObj);
        localStorage.setItem("taskList",JSON.stringify(templist))
        setTaskList(templist); 
        setModal(false);
    }

    return (
     <>
      <div className="contant border  text-center   m-auto ">
          
        {/* section header */}
        <section className='header'>
            <div>
                <h3 className='fs-1'>Todo List</h3> 
                <button className='btn  text-white rounded-pill mt-4 w-100 p-3' onClick={()=>{setModal(true)}}>Create Task</button>
            </div>
        </section>
           {/*section task-container  */}
        <section className='task-container  '>
           <div className="container w-75 mt-2">
           <div className="row  d-flex justify-content-center">
            {taskList.map((obj,index) =>(
                <div className="col-md-4  mb-2"><Card taskObj={obj} index={index} deleteTask={deleteTask} updateListArray={updateListArray}/></div>
            ))}
            
            </div>
           </div>
          
        </section>   
         <CreateTask toggle={toggle} modal={modal} save={saveTask}/> 
      </div>
     </>
    );
};

export default Todolist;