import React, { useState ,useEffect} from 'react';
import  Joi from "joi";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
const EditTask = ({modal , toggle , updateTask}) => {
    const[taskName ,setTaskName]= useState('');
    const[description ,setDescription]= useState('');
    const handleChange = (e) => {
      const {name , value} =e.target;
      if (name === 'taskName') {
         setTaskName(value);
      }else{
        setDescription(value)
      };
       
    };
    // useEffect
    useEffect(()=>{
     setTaskName(taskObj.Name);
     setDescription(taskObj.Desc) 
  },[ ]) ;
    let taskObj ={};
    taskObj["Name"] = taskName;
    taskObj["Desc"] = description;
   // validation function
   const validateInputs =()=>{
    const schema = Joi.object({
      'Name':Joi.string().alphanum().min(3).max(20).required(),
      'Desc':Joi.string().min(10).max(30).required()
    })
    return schema.validate(taskObj,{abortEarly:false});
  };
  let testValidation = validateInputs();
    //saving data
    const handleUpdate =(e)=>{
      e.preventDefault();
      
      if (!testValidation.error) {
        updateTask(taskObj);
      }else{
       alert(Error('Please Update Your Task....'))
      }
     
    }
    return (
        <>
           <Modal isOpen={modal} toggle={toggle}>
        <ModalHeader toggle={toggle} className='text-black text-opacity-75'>Update Task</ModalHeader>
        <ModalBody>
          <form>
              <div className="form-group">
                <label htmlFor="taskName" className='text-black text-opacity-75'> Update Name</label>
                <input type="text" className="form-control" id="taskName" value ={taskName} onChange={handleChange} name='taskName' />
              </div>
                <div className="form-group mt-3">
                  <textarea type="text" rows="5" className="form-control" placeholder='Description...' value={description} name='description'
                  onChange={handleChange}></textarea>
                </div>
          </form>
        </ModalBody>
        <ModalFooter>
          <Button className='border-0 shadow-none rounded-pill w-25 p-2' onClick={handleUpdate}>
            Update
          </Button>{' '}
          <Button className='border-0 shadow-none rounded-pill w-25 p-2' onClick={toggle}>
            Cancel
          </Button>
        </ModalFooter>
      </Modal>
        </>
    );
};

export default EditTask;