import React ,{ useState } from 'react';
import EditTask from '../models/EditTask';
const Card = ({taskObj,index,deleteTask,updateListArray}) => {
     const [modal, setModal] = useState(false);
    const colors = [
        {
            primaryColor : "#5D93E1",
            secondaryColor : "#ECF3FC"
        },
        {
            primaryColor : "#F9D288",
            secondaryColor : "#FEFAF1"
        },
        {
            primaryColor : "#5DC250",
            secondaryColor : "#F2FAF1"
        },
        {
            primaryColor : "#F48687",
            secondaryColor : "#FDF1F1"
        },
        {
            primaryColor : "#B964F7",
            secondaryColor : "#F3F0FD"
        }
    ];
    const toggle=()=>{
        setModal(!modal)
    };
    //updatedFunction
    const updateTask=(obj)=>{
       updateListArray(obj,index)
    }
    // fun delete Task
    const handleDelete=()=>{
        deleteTask(index)
    };
    
    return (
        // start card-wrapper
        <div className="d-flex justify-content-center ">
            <div class = "card-wrapper rounded  text-start ">
            <div class = "card-top" style={{"background-color":colors[index%5].primaryColor,'width':'100%' ,height:'1px'}}></div>
            <div class = "task-holder">
            {/* taskName */}
            <span class = "card-header shadow-sm" style={{"background-color":colors[index%5].secondaryColor,"border-radius":"10px" ,'max-width':'80px',height:'30px'}}>{taskObj.Name}</span>
             {/* end taskName */}
                <p className = "mt-3">{taskObj.Desc}</p>

                <div style={{"position": "absolute", "right" : "20px", "bottom" : "20px"}}>
                    <i class = "far fa-edit fs-4 me-3" style={{"color" : colors[index%5].primaryColor, "cursor" : "pointer"}} onClick={()=> setModal(true)} ></i>
                    <i class="fas fa-trash-alt fs-4" style = {{"color" : colors[index%5].primaryColor, "cursor" : "pointer"}} onClick={handleDelete}></i>
                </div>  
        </div>
         <EditTask toggle={toggle}  updateTask={updateTask} modal={modal} taskObj={taskObj}/>
        </div>
        </div>
        // start card-wrapper
    );
};

export default Card;